.item {
  list-style: none;
}
.link {
  cursor: pointer;
  text-decoration: none;
  color: var(----text-welcome);
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  font-size: 0.938rem;
  line-height: 160%;
  transition: all ease-in-out 0.3s;

  &:hover {
    color: var(--color-links-hover);
  }
  &:active {
    pointer-events: none;
    color: var(--color-links-hover);
    border-bottom: 0.188rem solid var(--color-primary);
    transition: border-bottom ease-out 0.14s;
  }
}
