.footer {
  padding: 40px 0 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 2.5rem;

  .wrapperLink {
    display: flex;
    justify-content: space-between;
    column-gap: 5%;
  }

  .wrapperLogo{
    display: flex;
    position: relative;
    max-width: 100%;
    justify-content: end;
    align-items: end;


    .logo{
        max-width: 100%;
    }
  }
}
